.c-gallery__card {
    width: 300px;
    width: calc( 100% - 20px );
    max-width: 100%;
    height: 120px;
    border-radius: 10px;
    background: #f5f5f5;
    background-size: cover !important;
    background-position: center !important;
    padding: 10px;
    margin: 0 10px;
    box-sizing: border-box;
    box-shadow: 0 6px 14px rgba( 0, 0, 0, 0.3 );
    position: relative;
    display: inline-block;
    vertical-align: middle;
    overflow: hidden;
}

.c-gallery__card:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb( 0, 0, 0, 0.3 );
    background: linear-gradient( 0deg, rgba( 0, 0, 0, 0.8 ) 0%, rgba( 0, 0, 0, 0.1 ) 50%, rgba( 0, 0, 0, 0 ) 100% );
}

.c-gallery__card:not( :last-child ) {
    margin-right: 20px;
}

.c-gallery__type-text {
    font-family: 'Arial Rounded MT Pro Cyr';
    font-size: 12px;
    color: rgba( 255, 255, 255, 0.8 );
    text-shadow: 0px 3px 7px #000;
}

.c-gallery__main-content {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 10px;
    box-sizing: border-box;
}

.c-gallery__title,
.c-gallery__subtitle {
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 14px;
    color: #fff;
}

.c-gallery__title {
    font-family: 'Arial Rounded MT Pro Cyr';
}