.lang__flag {
	display: inline-block;
	vertical-align: middle;
	margin-bottom: 2px;
	margin-right: 3px;
	position: relative;
	border-radius: 4px;
	overflow: hidden;
	width: 18px;
	height: 14px;
	background-size: cover !important;
	background-position: center !important;
}

.lang__flag:after {
	content: '';
	position: absolute;
	top: -0;
	left: 0;
	width: 100%;
	height: 100%;
	border: 1px solid rgba( 0, 0, 0, 0.05 );
	border-radius: 4px;
	box-sizing: border-box;
}